@import "default";

.table-wrapper {
    display: flex;
    position: relative;
    width: 100%;
    padding: 0px 24px 24px 24px;
    background-color: #FAFAFA;  
}

table {
    display: flex;
    position: relative;
    flex-wrap: wrap;
    flex-direction: column;
    width: 100%;
    border: 1px solid rgb(243, 244, 246);
    border-width: 1px;
    background-color: white;
}
    
tbody {
    display: flex;
    position: relative;
    flex-wrap: wrap;
    flex-direction: column;
    width: 100%;
    tr {
        background-color: #FAFAFA;
        &:hover {
            background-color: #ebeaea;
        }
        &:nth-child(2n) {
            background-color: white;
            &:hover {
                background-color: #ebeaea;
            }
        }

    }
}
    
th {
    display: flex;
    position: relative;
    flex-wrap: wrap;
    flex-direction: row;
    width: 100%;
    text-align: left;
    padding: 2px 4px;
    font-family: 'TTHoves-DemiBold', sans-serif;
    font-size: 16px;
    padding: 8px 12px;

}

tr {
    display: flex;
    height: 50px;
    text-align: center;
    vertical-align: middle;
    align-items: center;
    border-bottom: 1px solid rgb(243, 244, 246);
}

td {
    display: flex;
    position: relative;
    flex-wrap: wrap;
    width: 100%;
    font-family: 'TTHoves-Regular', sans-serif;
    font-size: 16px;
    padding: 8px 12px;
}

