.page-wrapper {
    display: flex;
    float: right;
    position: relative;
    text-align: center;
    vertical-align: middle;
    align-items: center;
    justify-content: center;
    padding: 4px;
    a {
        display: flex;
        height: 34px;
        width: 34px;
        position: relative;
        justify-content: center;
        margin-right: 12px;
        border-radius: 0.25rem;
        cursor: pointer;
        &:hover {
            background-color: #ebeaea;
        }
        span {
            display: flex;
            position: relative;
            align-items: center;
        }
    }
}

.table-ico {
    display: flex;
    height: 34px;
    position: relative;
    text-align: center;
    vertical-align: middle;
    align-items: center;
    justify-content: center;
    margin-left: 12px;
}

.action-add {
    display: flex;
    height: 34px;
    width: 34px;
    position: relative;
    text-align: center;
    vertical-align: middle;
    align-items: center;
    justify-content: center;
    margin-right: 16px;
    border: 3px solid black;
    border-radius: 0.25rem;
    cursor: pointer;
    &:hover {
        background-color: #ebeaea;
    }
}
