* {
    font-family: 'TTHoves-Regular', sans-serif;
    color: $gray2;
}
.container {
    width: 1170px;
}
.button-orange {
    height: 50px;
    width: 190px;
    padding: 15px 45px;
    background: linear-gradient(89.65deg, #FF6753 0.26%, #FF8F50 99.7%);
    box-shadow: 0px 10px 20px rgba(255, 103, 83, 0.4);
    border-radius: 50px;
    border: 0px;
    color: $btnOrange;
    font-family: 'TTHoves-DemiBold', sans-serif;
    &:hover {
        color: $white;
    }
}
.button-orange_topnav {
    height: 50px;
    width: 170px;
    padding: 15px 30px;
    background: linear-gradient(89.65deg, #FF6753 0.26%, #FF8F50 99.7%);
    border-radius: 50px;
    border: 0px;
    color: $btnOrange;
    font-family: 'TTHoves-DemiBold', sans-serif;
    &:hover {
        color: $white;
    }
}
.button-transparent {
    height: 50px;
    width: 190px;
    padding: 15px 45px;
    background: transparent;
    border-radius: 50px;
    border: 2px solid $white;
    color: $white;
    font-family: 'TTHoves-DemiBold', sans-serif;
}

.dropdown-toggle::after {
    display: inline-block;
    margin-left: 6px;
    vertical-align: 1px;
    content: "";
    border-top: 5px solid;
    border-right: 3px solid transparent;
    border-bottom: 0;
    border-left: 3px solid transparent;
    border-radius: 1px;
}

.dropdown-menu[data-bs-popper] {
    margin-top: 23px;
    min-width: 100%;
}

.extrabold-title {
    font-family: 'TTHoves-ExtraBold';
    font-style: normal;
    font-weight: 800;
    font-size: 64px;
    line-height: 64px;
    color: $headersColor;
    padding: 0;
    margin: 0;
}

.bold-title {
    font-family: 'TTHoves-Bold';
    font-style: normal;
    font-weight: bold;
    font-size: 40px;
    line-height: 50px;
    text-transform: uppercase;
    color: $gray2;
    padding: 0;
    margin: 0;
}

.description {
    display: flex;
    margin: 30px 0px;
    font-family: 'TTHoves-Medium';
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 28px;
    text-align: justify;
    color: $gray2;
    padding: 0;
}
.description-bold {
    display: flex;
    margin: 30px 0px;
    font-family: 'TTHoves-Bold';
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 28px;
    text-align: justify;
    color: $gray2;
    padding: 0;
}

.mar-ri {
    margin-right: 30px;
}

